import React, { Fragment } from 'react';
import { withRouter } from 'react-router-dom';

import ThePost from '../partials/ThePost.js';

const BlogPage = ({ content, startIdx, endIdx, perSet, total }) => {
	let existEnd2End = content && (content.length - 1) === endIdx;
	let fullLoad = content && content.length === total;

	console.log('existEnd2End: ', existEnd2End);
	console.log('fullLoad: ', fullLoad);
	console.log('/////////////////////');

	return (
		<Fragment>{
			fullLoad || existEnd2End ? (
				content.map((post, idx) => {
					console.log('startIdx: ', startIdx);
					console.log('endIdx: ', endIdx);

					let renderCheck = idx >= startIdx && idx <= endIdx;
					return renderCheck ? <ThePost key={ post.id } data={ post }  idx={ idx } /> : '';
					// return <ThePost key={ post.id } data={ post }  idx={ idx } />;
				})
			) : (
				<h3>Loading...</h3>
			)
		}</Fragment>
	);
}

export default withRouter(BlogPage);